.modal {
  width: 94vw;
  min-height: 581px;
  max-width: 450px;
  z-index: 7;
  transform-origin: 0 0;
  background-color: #fff5f6;
  border-radius: 20px;
  padding: 40px 20px 20px;
  transition-property: transform, opacity;
  transition-duration: .45s, .45s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1), cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

